import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  List,
  ListItem,
  Button,
  Chip,
  Alert,
  IconButton,
  Tooltip,
  CircularProgress,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Divider
} from '@mui/material';
import {
  CheckCircle as CheckCircleIcon,
  Error as ErrorIcon,
  Help as HelpIcon,
  Settings as SettingsIcon,
  Refresh as RefreshIcon,
  Security as SecurityIcon,
  Info as InfoIcon
} from '@mui/icons-material';
import { autoWebViewJs, isRealInAndroid } from '@droid/android';

interface Permission {
  id: string;
  name: string;
  type: 'runtime' | 'special' | 'vendor';
  status: 'granted' | 'denied' | 'unknown';
  canDetect: boolean;
  description: string;
  settingsAction: string;
}

interface PermissionsData {
  permissions: Permission[];
  timestamp: number;
  deviceBrand: string;
  apiLevel: number;
  error?: string;
}

export const PermissionStatusPanel: React.FC = () => {
  const [permissionsData, setPermissionsData] = useState<PermissionsData | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [infoDialogOpen, setInfoDialogOpen] = useState(false);
  const [selectedPermission, setSelectedPermission] = useState<Permission | null>(null);

  const fetchPermissions = async () => {
    try {
      if(!isRealInAndroid) return;
      setLoading(true);
      setError(null);
      
      const script = `
        com.fanfanlo.emergencycall.manager.PermissionJSInterface.getAllPermissionsStatus();
      `;
      
      const { javaResultString } = autoWebViewJs.callScript(script);
      
      if (!javaResultString) {
        throw new Error('No response from permission interface');
      }
      
      const data = JSON.parse(javaResultString) as PermissionsData;
      
      if (data.error) {
        throw new Error(data.error);
      }
      
      setPermissionsData(data);
      console.log('Permissions data loaded:', data);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Unknown error';
      setError(errorMessage);
      console.error('Error fetching permissions:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPermissions();
  }, []);

  const handleOpenSettings = (permission: Permission) => {
    try {
      const script = `
        com.fanfanlo.emergencycall.manager.PermissionJSInterface.openPermissionSettings("${permission.id}");
      `;
      autoWebViewJs.callScript(script);
      console.log(`Opening settings for permission: ${permission.id}`);
    } catch (err) {
      console.error('Error opening permission settings:', err);
    }
  };

  const handleConfirmLockScreenPermission = () => {
    try {
      const script = `
        com.fanfanlo.emergencycall.manager.PermissionJSInterface.confirmLockScreenPermission();
      `;
      autoWebViewJs.callScript(script);
      console.log('Confirmed lock screen permission');
      // 
      setTimeout(() => fetchPermissions(), 500);
    } catch (err) {
      console.error('Error confirming lock screen permission:', err);
    }
  };

  const handleShowPermissionInfo = (permission: Permission) => {
    setSelectedPermission(permission);
    setInfoDialogOpen(true);
  };

  const handleCloseInfoDialog = () => {
    setInfoDialogOpen(false);
    setSelectedPermission(null);
  };

  const getPermissionReason = (permission: Permission): string => {
    const reasonMap: Record<string, string> = {
      'android.permission.ACCESS_FINE_LOCATION': '',
      'android.permission.ACCESS_COARSE_LOCATION': '',
      'android.permission.BODY_SENSORS': '',
      'android.permission.CAMERA': '',
      'android.permission.RECORD_AUDIO': '',
      'android.permission.ACTIVITY_RECOGNITION': '()',
      'overlay': '',
      'lock_screen_popup': '',
      'lock_screen_display': '',
      'desktop_shortcut': ''
    };
    return reasonMap[permission.id] || '';
  };

  const getPermissionImpact = (permission: Permission): string => {
    const impactMap: Record<string, string> = {
      'android.permission.ACCESS_FINE_LOCATION': ' ',
      'android.permission.ACCESS_COARSE_LOCATION': ' ',
      'android.permission.BODY_SENSORS': ' ',
      'android.permission.CAMERA': ' ',
      'android.permission.RECORD_AUDIO': ' ',
      'android.permission.ACTIVITY_RECOGNITION': ' ',
      'overlay': ' ',
      'lock_screen_popup': ' ',
      'lock_screen_display': ' ',
      'desktop_shortcut': ' '
    };
    return impactMap[permission.id] || '';
  };

  const getStatusIcon = (permission: Permission) => {
    const iconProps = { fontSize: 18 };
    switch (permission.status) {
      case 'granted':
        return <CheckCircleIcon sx={{ color: 'success.main', ...iconProps }} />;
      case 'denied':
        return <ErrorIcon sx={{ color: 'error.main', ...iconProps }} />;
      case 'unknown':
        return <HelpIcon sx={{ color: 'warning.main', ...iconProps }} />;
      default:
        return <HelpIcon sx={{ color: 'grey.500', ...iconProps }} />;
    }
  };

  const getStatusChip = (permission: Permission) => {
    const statusConfig = {
      granted: { label: '', color: 'success' as const },
      denied: { label: '', color: 'error' as const },
      unknown: { label: '?', color: 'warning' as const }
    };

    const config = statusConfig[permission.status];
    return (
      <Chip 
        label={config.label} 
        color={config.color} 
        size="small"
        sx={{ 
          minWidth: 24,
          height: 20,
          fontSize: '0.7rem',
          '& .MuiChip-label': { px: 0.5 }
        }}
      />
    );
  };

  const getTypeLabel = (type: string) => {
    const typeLabels = {
      runtime: '',
      special: '',
      vendor: ''
    };
    return typeLabels[type as keyof typeof typeLabels] || type;
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="120px">
        <CircularProgress size={20} />
        <Typography variant="caption" sx={{ ml: 1 }}>...</Typography>
      </Box>
    );
  }

  if (error) {
    return (
      <Alert severity="error" sx={{ py: 1 }}>
        <Typography variant="body2">: {error}</Typography>
        <Button onClick={fetchPermissions} size="small" sx={{ mt: 0.5 }}>
          
        </Button>
      </Alert>
    );
  }

  if (!permissionsData) {
    return (
      <Alert severity="info" sx={{ py: 1 }}>
        <Typography variant="body2"></Typography>
      </Alert>
    );
  }

  const { permissions, deviceBrand, apiLevel } = permissionsData;

  return (
    <Paper elevation={1} sx={{ p: 1 }}>
      {/*  */}
      <Box display="flex" alignItems="center" justifyContent="space-between" mb={1}>
        <Box display="flex" alignItems="center">
          <SecurityIcon sx={{ mr: 0.5, color: 'primary.main', fontSize: 18 }} />
          <Typography variant="body2" fontWeight="medium"></Typography>
        </Box>
        <Tooltip title="">
          <IconButton onClick={fetchPermissions} size="small" sx={{ p: 0.5 }}>
            <RefreshIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </Box>

      {/*  */}
      <Alert severity="info" sx={{ mb: 1, py: 0.5 }}>
        <Typography variant="caption" display="block">
            
        </Typography>
        <Typography variant="caption" color="textSecondary">
          {deviceBrand}  API{apiLevel}
        </Typography>
      </Alert>

      {/*  */}
      <List sx={{ py: 0 }}>
        {permissions.map((permission) => (
          <ListItem
            key={permission.id}
            sx={{
              borderLeft: 2,
              borderLeftColor: 
                permission.status === 'granted' ? 'success.main' :
                permission.status === 'denied' ? 'error.main' : 'warning.main',
              mb: 0.5,
              py: 0.5,
              px: 0.5,
              borderRadius: 0.5,
              bgcolor: 'grey.50',
              display: 'block' // block
            }}
          >
            {/*  +  +  +  */}
            <Box display="flex" alignItems="center" mb={0.5}>
              <Box sx={{ mr: 0.5 }}>
                {getStatusIcon(permission)}
              </Box>
              <Typography 
                variant="body2" 
                fontWeight="medium" 
                sx={{ flex: 1, minWidth: 0 }}
              >
                {permission.name}
              </Typography>
              <Tooltip title="">
                <IconButton
                  size="small"
                  onClick={() => handleShowPermissionInfo(permission)}
                  sx={{ mr: 0.5, p: 0.25 }}
                >
                  <InfoIcon sx={{ fontSize: 16, color: 'primary.main' }} />
                </IconButton>
              </Tooltip>
              {getStatusChip(permission)}
            </Box>
            
            {/*  +  */}  
            <Box display="flex" alignItems="center" justifyContent="space-between" mb={0.5}>
              <Box display="flex" alignItems="center" gap={0.5}>
                <Chip 
                  label={getTypeLabel(permission.type)} 
                  variant="outlined" 
                  size="small"
                  sx={{ 
                    fontSize: '0.65rem',
                    height: 18,
                    '& .MuiChip-label': { px: 0.5, py: 0 }
                  }}
                />
                {!permission.canDetect && (
                  <Typography variant="caption" color="textSecondary" sx={{ fontSize: '0.65rem' }}>
                    
                  </Typography>
                )}
              </Box>
            </Box>
            
            {/*  */}
            <Box display="flex" gap={0.5}>
              {/* {(permission.id === 'lock_screen' || permission.id === 'lock_screen_popup' || permission.id === 'lock_screen_display') && permission.status === 'unknown' && (
                <Button
                  variant="outlined"
                  size="small" 
                  color="success"
                  onClick={handleConfirmLockScreenPermission}
                  sx={{ 
                    fontSize: '0.65rem',
                    minWidth: 'auto',
                    px: 0.5,
                    py: 0.25,
                    height: 24
                  }}
                >
                  
                </Button>
              )} */}
              <Button
                variant="outlined"
                size="small"
                startIcon={<SettingsIcon sx={{ fontSize: 12 }} />}
                onClick={() => handleOpenSettings(permission)}
                sx={{ 
                  fontSize: '0.65rem',
                  minWidth: 'auto',
                  px: 0.5,
                  py: 0.25,
                  height: 24
                }}
              >
                
              </Button>
            </Box>
          </ListItem>
        ))}
      </List>

      {/*  */}
      <Box mt={0.5} py={0.25} px={0.5} bgcolor="grey.100" borderRadius={0.25}>
        <Typography variant="caption" color="textSecondary" sx={{ fontSize: '0.6rem' }}>
          : {new Date(permissionsData.timestamp).toLocaleTimeString()}
        </Typography>
      </Box>

      {/*  */}
      <Dialog
        open={infoDialogOpen}
        onClose={handleCloseInfoDialog}
        maxWidth="sm"
        fullWidth
        PaperProps={{
          sx: { mx: 1 } // 
        }}
      >
        <DialogTitle sx={{ pb: 1 }}>
          <Box display="flex" alignItems="center">
            <SecurityIcon sx={{ mr: 1, color: 'primary.main' }} />
            <Box>
              <Typography variant="h6" component="div">
                {selectedPermission?.name}
              </Typography>
              <Typography variant="caption" color="textSecondary">
                {selectedPermission && getTypeLabel(selectedPermission.type)}
              </Typography>
            </Box>
          </Box>
        </DialogTitle>
        
        <DialogContent sx={{ pt: 0 }}>
          {selectedPermission && (
            <Box>
              {/*  */}
              <Box display="flex" alignItems="center" mb={2}>
                <Typography variant="subtitle2" sx={{ mr: 1 }}>:</Typography>
                <Box display="flex" alignItems="center">
                  {getStatusIcon(selectedPermission)}
                  <Typography variant="body2" sx={{ ml: 0.5 }}>
                    {selectedPermission.status === 'granted' ? '' : 
                     selectedPermission.status === 'denied' ? '' : ''}
                  </Typography>
                </Box>
              </Box>
              
              <Divider sx={{ mb: 2 }} />
              
              {/*  */}
              <Box mb={2}>
                <Typography variant="subtitle2" gutterBottom color="primary.main">
                   
                </Typography>
                <Typography variant="body2" color="textSecondary" lineHeight={1.6}>
                  {getPermissionReason(selectedPermission)}
                </Typography>
              </Box>
              
              <Divider sx={{ mb: 2 }} />
              
              {/*  */}
              <Box mb={1}>
                <Typography variant="subtitle2" gutterBottom color="warning.main">
                   
                </Typography>
                <Typography variant="body2" color="textSecondary" lineHeight={1.6}>
                  {getPermissionImpact(selectedPermission)}
                </Typography>
              </Box>
            </Box>
          )}
        </DialogContent>
        
        <DialogActions sx={{ px: 3, pb: 2 }}>
          <Button onClick={handleCloseInfoDialog} color="primary">
            
          </Button>
          {selectedPermission && (
            <Button
              variant="contained"
              onClick={() => {
                handleOpenSettings(selectedPermission);
                handleCloseInfoDialog();
              }}
              color="primary"
            >
              
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </Paper>
  );
};